import { Values } from '../components/ModalForm/ModalForm';
import { todoList } from './todoList';

interface Edit{
  id: number;
  value: Values;
}

interface Actions {
  type: string;
  payload?:
    | Values
    | Edit
    | number
    | string
    | [number, number | null]
    | [string, number | null];
  edit?: Edit;
  id?: number;
}

class TaskItem {
  static _lastId: number | null = null;
  static _autoId: boolean = true;
  public id: number;
  public titleDescription: string;
  public description: string;
  public createdAt: string;
  public updatedAt: string;
  public priority: string;
  public isCompleted: boolean;
  constructor(values: Values, lastId: number = 1, isAutoID?: boolean) {
    TaskItem._autoId = (isAutoID === undefined)? true: isAutoID;
    if (TaskItem._lastId === null) TaskItem._lastId = lastId;
    this.id = (TaskItem._autoId)? this._createNewID() : lastId;
    this.titleDescription = values.inputValue;
    this.description = values.memoValue;
    this.createdAt = TaskItem._getNewDate();
    this.updatedAt = TaskItem._getNewDate();
    this.priority = this._convertToString(values.radioBox);
    this.isCompleted = false;
  }
  _createNewID(): number {
    if (TaskItem._lastId !== null) TaskItem._lastId += 1;
    else TaskItem._lastId = 1;
    return TaskItem._lastId;
  }
  _convertToString(p: Array<boolean>): string {
    const priority = ['normal', 'high', 'urgent'];
    return priority[p.findIndex((i) => i)];
  }
  static _getNewDate(){
    const date = new Date();
    const D = date.getDate().toString().padStart(2,'0');
    const M = date.getMonth().toString().padStart(2,'0');
    const Y = date.getFullYear().toString();
    const h = date.getHours().toString().padStart(2,'0');
    const m = date.getMinutes().toString().padStart(2,'0');
    const s = date.getSeconds().toString().padStart(2,'0');
    const dateString = `${D}.${M}.${Y} - ${h}:${m}:${s}`;
    return dateString;
  }
  update(values: Values){
    this.titleDescription = values.inputValue;
    this.description = values.memoValue;
    this.updatedAt = TaskItem._getNewDate();
    this.priority = this._convertToString(values.radioBox);
  }
  static init(id:number){
    TaskItem._lastId = id;
  }
}

function loadTasklist(todoList:TasksList[]){
  const LAST_ELEMENT = todoList.length - 1;
  const result = todoList.map((t: TasksList) => {
    t.__proto__ = TaskItem.prototype;
    return t;
  });
  const lastID: number = result.sort((a,b) =>a.id - b.id)[LAST_ELEMENT].id;
  // console.log('result ID:',lastID);
  TaskItem.init(lastID);
  return result;
}

const initState = {
  currentCategoryId: 1,
  currentTaskId: 1,
  isVisibleModal: false,
  lastCatId: 1,
  lastTaskId: 8,
  categoryes: [
    {
      id: 1,
      description: 'Category 1',
      isCompleted: false,
      createdAt: new Date().toLocaleString('en-US'),
      tasksList: loadTasklist(todoList),
    },
  ],
};

export interface TasksList {
  id: number;
  titleDescription: string;
  description: string;
  isCompleted: boolean;
  createdAt: string;
  updatedAt: string;
  priority: string;
  __proto__?: any;
  update?: (values: Values) => void;
  init?: (id: number) => void;
}

export interface Categoryes {
  id: number;
  description: string;
  createdAt: string;
  isCompleted: boolean;
  tasksList: Array<TasksList>;
}

export interface StateInit {
  currentCategoryId: number | null;
  currentTaskId: number;
  isVisibleModal: boolean;
  lastCatId: number;
  lastTaskId: number;
  categoryes: Array<Categoryes>;
}


class CategoryItem {
  static _lastId: number | null = null;

  public id: number;
  public description: string;
  public isCompleted: boolean;
  public createdAt: string;
  public tasksList: [];
  constructor(title: string, lastId: number = 1) {
    if (CategoryItem._lastId === null) {
      CategoryItem._lastId = lastId;
    }
    this.id = this.createNewID();
    this.description = title;
    this.isCompleted = false;
    this.createdAt = this.getDate();
    this.tasksList = [];
  }
  getDate() {
    return new Date().toLocaleString('en-US');
  }
  createNewID() {
    if (CategoryItem._lastId !== null) CategoryItem._lastId += 1;
    else CategoryItem._lastId = 1;
    return CategoryItem._lastId;
  }
}

function numberFrom(str: string) {
  const pr = ['normal', 'high', 'urgent'];
  const idx = pr.indexOf(str);
  return idx !== -1 ? idx : 0;
}

function sortTasksByPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
      return numberFrom(b.priority) - numberFrom(a.priority);
    });
  }
  return true;
}

function sortTasksByIdOfPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (a.priority === b.priority) {
        if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
        return numberFrom(b.priority) - numberFrom(a.priority);
      }
      return 0;
    });
  }
  return true;
}

