import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Draggable } from 'react-beautiful-dnd';
import editIcon from '../../img/edit.png';
import deleteIcon from '../../img/delete.png';

interface CItemProps {
  idx: number;
  keyDown: () => void;
  itemClick: (itemId: number) => void;
  editItem: (itemId: number) => void;
  itemDescription: string;
  itemId: number;
  curCatId: number;
  innerRef?: any;
  deleteItem: (id: number) => void;
  createdAt: string;
}

const cutToshortString = (str: string): string => {
  return str.length > 18 ? str.slice(0, 17).concat('...') : str;
};

const CategoryItem: React.FC<CItemProps> = (props) => {
  const {
    idx,
    keyDown,
    editItem,
    itemClick,
    itemDescription,
    itemId,
    deleteItem,
    curCatId,
    createdAt,
  } = props;
 
  return (
    <Draggable
      index={idx}
      key={`itemId-${'dragble'}`}
      draggableId={`item-${itemId}`}
    >
      {(provided, snapshot) => (
        <div
          key={itemId}
          ref={provided.innerRef}
          role="button"
          tabIndex={idx}
          onKeyDown={keyDown}
          className={`cg-item ${curCatId === itemId ? 'active' : ''}`}
          onClick={() => itemClick(itemId)}
          {...provided.dragHandleProps}
          {...provided.draggableProps}
        >
          <p>{cutToshortString(itemDescription)}</p>
          <span>
            <img src={editIcon} width="18" onClick={() => editItem(itemId)} />
            <img
              src={deleteIcon}
              width="18"
              onClick={() => deleteItem(itemId)}
            />
          </span>
          <p>{createdAt}</p>
        </div>
      )}
    </Draggable>
  );
};

export default CategoryItem;