/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useState } from 'react';
import './index.scss';
import { connect } from 'react-redux';
import { StateInit, Categoryes } from '../store/reducer';
import TodoCategories from './TodosCategory/TodoCategory';
import TasksTable from './TasksTable/TasksTable';
import ModalForm, { Values, ErrorObject } from './ModalForm/ModalForm';

interface Props {
  state: StateInit;
  addTask: (values: Values) => void;
  // removeModal: () => void;
}

interface Dispatch {
  (type: { type: string; payload?: Values }): void;
}

function getCurrentItem(catList: Categoryes[], catId: number | null) {
  const currentItemEmpty = {
    description: '',
    tasksList: [],
  };
  const list = catList.filter((item: Categoryes) => item.id === catId)[0];
  if (list) return list;
  return currentItemEmpty;
}

function Todos(props: Props) {
  const {
    addTask,
    state,
    // removeModal,
  } = props;
  // const { isVisibleModal } = state;
  const [visible, setVisible] = useState(false);
  const currentItem = getCurrentItem(state.categoryes, state.currentCategoryId);
  const categoryTitle: string = currentItem.description;
  const okClick = (values: Values) => {
    addTask(values);
    setVisible(false);
  };
  const cancelClick = () => {
    setVisible(false);
  };
  const showModalClick = () => {
    setVisible(true);
  };

  return (
    <>
      {visible && (
        <ModalForm
          inputData={{
            formTitle: 'Create new category',
            inputBox: {
              name: 'category',
              label: 'Input category name',
              placeholder: 'for example: Home work',
              maxlen: 20,
              value: '',
              isRequired: true,
            },
            onOK: okClick,
            onCancel: cancelClick,
          }}
        >
          {(error: ErrorObject) =>
            error && (
              <p>
                {error.input &&
                  error.input.empty &&
                  'Need to input category name!'}
                {error.input &&
                  error.input.maxlen &&
                  'Must be less then 20 chars!'}
              </p>
            )
          }
        </ModalForm>
      )}
      <div className="container">
        <TodoCategories showModalForm={showModalClick} />
        {currentItem && (
          <TasksTable
            categoryTitle={categoryTitle}
            tasksList={currentItem.tasksList}
          />
        )}
      </div>
    </>
  );
}

const mapStetaToProps = (state: StateInit) => ({ state });
const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    addTask: (values: Values) =>
      dispatch({ type: 'ADD_CATEGORY', payload: values }),
    // removeModal: () => dispatch({ type: 'REMOVE_MODAL' }),
  };
};

const connector = connect(mapStetaToProps, mapDispatchToProps);

export default connector(Todos);
