import React from 'react';
import { connect } from 'react-redux';

interface Props {
  showModalForm: () => void;
}

function CategoryAddButton(props: Props) {
  const { showModalForm } = props;
  const showModalClick = () => {
    showModalForm();
  };

  return (
    <div className="cg-button">
      <button onClick={showModalClick} type="button">
        Add todos
      </button>
    </div>
  );
}

